.ability{
  background: #284987;
  text-align: center;
}
.ability h2{
  font-size: 45px;
  text-transform: uppercase;
  color: transparent;    
  -webkit-background-clip: text;
  background-clip: text;
  font-weight: 900;
  background-image: linear-gradient(to right, #c4dcd8,#c4dcd8,#67c9cb,#67c9cb);
  padding-top: 150px;
  transition: 0.35s;
  font-family: 'Varela Round', sans-serif;
}
.ability-main{
  display: flex;
  padding: 150px 50px;
  justify-content: space-between;
  flex-wrap: wrap;
}
.text-part{
  border-radius: 20px;
  background:#c4dcd8;
  margin-bottom: 30px;
  width: 330px;
  height: 375px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  transition: 0.35s;
  border: 2px solid #c4dcd8;
  
}
.ability-main h4{
  text-transform: uppercase;
  text-align: left;
  border-radius: 20px;
  color: #0c2960;
  font-family: 'Nunito', sans-serif;
  letter-spacing: 1px;
  transition: 0.45s;
  font-size: 20px;
  font-weight: 900;
  font-size: 25px;
  margin: 30px 15px;
}
.text-part img{
  width: 150px;
}
.text-part:hover h4{
  color: #320396;
  font-weight: bold;
}
.text-part:hover{
  border: none;
  box-shadow: 0 0 3em #909b99;
  transform: scale(1.0);
  background-image: linear-gradient(to bottom, #c4dcd8,#c4dcd8,#c4dcd8,#c4dcd8,#6681dd);
}
.text-part:hover .des-part p{
  background-image: linear-gradient(to right ,#6a83d6,#6a83d6,#1d44c3);
}
.abimg2,.abimg22,.abimg222,.abimg2222{
  opacity: 0;
  margin-left: -150px;
  transition: 0.45s;
}
.abimg1,.abimg11,.abimg111,.abimg1111{
  transition: 0.45s;
}
.text-part1:hover .abimg2{
  opacity: 1;
}
.text-part1:hover .abimg1{
  opacity: 0;
}
.text-part2:hover .abimg22{
  opacity: 1;
}
.text-part2:hover .abimg11{
  opacity: 0;
}
.text-part3:hover .abimg222{
  opacity: 1;
}
.text-part3:hover .abimg111{
  opacity: 0;
}
.text-part4:hover .abimg2222{
  opacity: 1;
}
.text-part4:hover .abimg1111{
  opacity: 0;
}
.bottom-img{
  height: 360px;
  align-self: center;
}
.des-part{
  display: flex;
  justify-content: space-between;
  width: 100%;
  padding-bottom: 10px;
}
.des-part p{
  text-align: left;
  width: 225px;
  color: transparent;    
  -webkit-background-clip: text;
  background-clip: text;
  font-weight: 900;
  font-size: 20px;
  font-family: 'Varela Round', sans-serif;
  background-image: linear-gradient(to right, #fff,#fff,#67c9cb,#67c9cb);
}
.img-part{
  background: #244685;
  border-top-right-radius:20px;
  border-top-left-radius:20px;
  padding-top: 30px;
}

