@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700;800;900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Joan&family=Nunito:ital,wght@0,300;0,400;0,500;0,600;0,700;1,300;1,400;1,700&family=Raleway:ital,wght@0,400;0,500;1,400&family=Varela+Round&display=swap&family=Rammetto+One');
* {
    box-sizing: border-box;
}
body {
  background-color: #244685;
}
html *,
html *:before,
html *:after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
a{
  text-decoration: none;
}
.all-margin {
  width: 2000px;
  margin: 0 auto;
}
.btn {
  position: relative;
  display: block;
  overflow: hidden;
  width: 250px;
  max-width: 250px;
  margin: 15px auto;
  text-transform: uppercase;
  border: 1.3px solid;
  transition: 0.35s ease-in-out;
  color: #111;
  font-family: 'Varela Round', sans-serif;
  font-size: 20px;
  border-radius: 40px;
  padding: 15px 0;
  text-transform: uppercase;
  letter-spacing: 2px;
  text-align: center;
}
.btn:hover {
  color: #fff;
  font-weight: 400;
  letter-spacing: 3.7px;
}
.btn span {
  position: absolute;
  display: block;
  width: 0;
  height: 0;
  border-radius: 50%;
  background-color: #67c9cb;
  transition: width 0.4s ease-in-out, height 0.4s ease-in-out;
  transform: translate(-50%, -50%);
  z-index: -1;
}
.btn:hover span {
  width: 225%;
  height: 562.5px;
}
.preloader {
  display: flex;
  justify-content: center;
  align-items: center;
}
.preloader-content {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}

::selection {
  color: #889ec5;
  background-color: #5d4395;
}
