.change-language {
  display: inline;
  font-size: 18px;
}
.selected {
  color: white;
}
.menu-a{
  color: #fff;
  margin: 0;
  transition: color 0.1s ease-in-out;
  padding: 10px 15px;
  border-color: #244685;
  cursor: pointer;
}
.menu-a span{
  background-color: #fff;
}
.menu-a:hover{
  color: #244685;
}
header{
  position: -webkit-sticky; /* Safari */
  position: sticky;
  z-index: 10;
  top: 0;
  width: 100%;
  background: #244685;
}
.header li{
  cursor: pointer;
}
.header{
  padding: 30px 50px 30px 70px;
  justify-content: space-around;
  display: flex;
  flex-direction: row;
  font-size: 25px;
  color: #fff;
}
.menu li{
  list-style: none;
  align-self: center;
  text-transform: uppercase;
  font-weight: 400;
  font-family: 'Nunito', sans-serif;
  letter-spacing: 2px;
  border-radius: 40px;
}
.menu {
    width: 100%;
    display: flex;
    justify-content: space-around;
    font-size: 25px;
    font-family: 'Work Sans', sans-serif;
    align-items: center;
}
.header-logo{
  width: 30%;
  align-self: center;
  margin-right: 180px;
  cursor: pointer;
}
.logo-menu{
  width: 65%;
}
.bi{
  margin-right: 15px;
}
.change-language {
  display: flex;
  color: rgb(134, 124, 124);
  font-weight: 400;
  font-family: 'Nunito', sans-serif;
  align-items: center;
  justify-content: center;
  padding: 0px 20px 0px 20px;
  cursor: pointer;
}
.interval {
  color: #fff;
}
.activeLanguage {
  color: #fff;
}
.hamburger-menu {
  display: none;
}
.pt-5 li{
  font-size: 40px;
}
.telephone-mobile {
  display: none;
}
.bi{
  color: #fff;
}
.menu .active{
  transition: 0.3s;
  background: #fff;
  font-weight: 400;
  letter-spacing: 3.7px;
  color: #244685;
}
.pt-5 .active{
  color: #244685;
}
.hamburger-logo{
cursor: pointer;
}














