footer{
  padding: 50px 50px;
  display: flex;
  background: #244685;
  align-items: center;
  justify-content: space-between;
}
.footer-logo{
  width: 200px;
}
.footer-logo img{
  width: 100%;
}
footer h2{
  color: #fff;
  font-size: 30px;
  text-transform: uppercase;  
  transition: 0.35s;
  font-family: 'Varela Round', sans-serif;
}
footer a{
  font-size: 20px;
  font-family: 'Varela Round', sans-serif;
  font-size: 25px;
  color: #fff;
  transition: 0.45s;
}
@media(max-width: 1350px) {
  footer {
    padding: 50px 90px 50px 90px;
  }
}
@media(max-width: 1024px) {
  .footer-logo {
    display: none;
  }
  footer h2 {
    font-size: 25px;
  }
}
@media(max-width: 750px) {
  footer {
    flex-direction: column;
    padding: 15px 20px 70px 20px;
  }
  footer a {
    padding-bottom: 30px
  }
}
@media(max-width: 550px) {
  footer h2 {
    font-size: 25px;
  }
}