.staf{
  padding: 80px 0 200px 0;
    background: #c4dcd8;
  }
  .staf h2{
    text-align: center;
    font-size: 45px;
    margin-bottom: 80px;
    color: transparent;    
    -webkit-background-clip: text;
    background-clip: text;
    background-image: linear-gradient(to left, #244685,#244685, #8af7fe);
    color: #244685;
    transition: 0.35s;
    text-transform: uppercase;
    font-family: 'Varela Round', sans-serif;
  }
  .members-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0px 50px;
  }
  .members{
    display: flex;
    justify-content: space-between;
    width: 100%;
    margin: 0 auto;
  }
  .member-name {
    text-align: center;
    width: 400px;
  }
  .member{
    width: 30%;
    text-align: center;
    border-radius: 30px;
    background: #f0f5f5;
    border:1px solid #f0f5f5;
    transition: ease-in-out 0.35s;
    box-shadow: 0 0 1em #67c9cb;
  }
  .memberimg{
    height: 260px;
    padding-top: 20px;
    margin-bottom: 30px;
  }
  .member h3{
      transition: ease-in-out 0.35s;
      padding-top: 15px;
      margin-bottom: 30px;
      font-size: 44px;
      text-align: center;
      font-weight: bold;
      color: transparent;    
      -webkit-background-clip: text;
      background-clip: text;
      background-image: linear-gradient(to left, #244685, #8af7fe);
        /*#b1e6f9, #e9cbcf) #8af7fe, #65a4fd);*/
      text-transform: uppercase;
      font-family: 'Varela Round', sans-serif;
  }
  .member p { 
      font-family: 'Varela Round', sans-serif;
      font-size: 23px;
      line-height: 45px;
      text-align: left;
      letter-spacing: 1px;
      padding: 0 30px;
      min-width: 400px;
  }
  .mem-wrapper {
    border-radius: 30px;
    padding: 0px 0px 40px 0px;
  }
  .member:hover .mem-wrapper{
    
    /* #4680bc, #334394*/
    transition: 3.5s;
    background: linear-gradient(to right, #244685, #67c9cb);
  }
  .member:hover .member-desc{
    color: transparent;    
    -webkit-background-clip: text;
    background-clip: text;
    background-image: linear-gradient(to right, #8af7fe,#fff,#fff);
  }
  .member:hover .member-title{
    color: #fff;
  }
  .member:hover{
    border:1px solid #244685;
    box-shadow: 0 0 2em #67c9cb;
    background: #eef5b5;
    transform: scale(1.03);
  }














