.contact{
  background: #c4dcd8;
}
.contact-main{
  display: flex;
  justify-content: center;
  padding: 200px 50px;
}
.contact h2{
  font-size: 45px;
  text-transform: uppercase;  
  color: #113372;
  transition: 0.35s;
  font-family: 'Varela Round', sans-serif;
  margin-bottom: 30px;
}
.contact-description{
  font-size: 20px;
  font-family: 'Varela Round', sans-serif;
  font-size: 20px;
  color: #193b7a;
  transition: 0.45s;
  margin-bottom: 30px;
}
form{
  display: flex;
  flex-direction: column;
  position: relative;
  z-index: 1;
}

input{
  padding: 30px 15px;
  margin-bottom: 15px;
  border-radius: 15px;
  border: none;
  outline: none;
}
input::placeholder,button,input{
  font-size: 20px;
  font-family: 'Varela Round', sans-serif;
  font-size: 22px;
  color: #062867;
  transition: 0.45s;
}
