.waves-footer{
  height: 85px;
}
button{
  width: 270px;
  height: 80px;
  border: none;
  outline: none;
  background: #244685;
  color: #fff;
  font-size: 22px;
  border-radius: 40px;
  text-align: center;
  box-shadow: 0 6px 20px -5px rgba(0,0,0,0.4);
  position: relative;
  overflow: hidden;
  cursor: pointer;
}

.check-box{
  width: 80px;
  height: 80px;
  border-radius: 40px;
  box-shadow: 0 0 12px -2px rgba(0,0,0,0.5);
  position: absolute;
  top: 0;
  right: -40px;
  opacity: 0;
}

.check-box svg{
  width: 40px;
  margin: 20px;
}

svg path{
  stroke-width: 3;
  stroke-dasharray: 34;
  stroke-dashoffset: 34;
  stroke-linecap: round;
}

#actived{
  background: #67c9cb;
  transition: 1s;
}

#actived .check-box{
  stroke:#fff;
  right: 0;
  opacity: 1;
  transition: 1s;
}

#actived p{
  margin-right: 125px;
  transition: 1s;
}

#actived svg path{
  stroke-dashoffset: 0;
  transition: 1s;
  transition-delay: 1s;
}
.invalid {
  box-shadow: 0 0 4px red;
}
.text-danger {
  color: red;
  font-size: 16px;
  padding-bottom: 20px;
}
.message {
  padding: 30px 15px 130px 15px;
}
.input-def {
  max-height: 100px;
}




@media(max-width: 1350px) {
  .contact-main {
    display: flex;
    justify-content: center;
    padding: 130px 50px;
    position: relative;
    z-index: 2;
  }
  .contact-left {
    padding-right: 80px;
    margin-right: 80px;
  }
  .contact h2 {
    font-size: 40px;
  }
  .contact-description{
    width: 576px;
    font-size: 22px;
    line-height: 36px;
  }
}
@media(max-width: 1024px) {
  .contact-main {
    flex-direction: column-reverse;
  }
  .contact-description {
    font-size: 29px;
    line-height: 52px;
    width: 100%;
  }
  .contact h2 {
    font-size: 50px;
  }
  .contact-left {
    padding-right: 0px;
    margin-right: 0px;
    display: flex;
    align-items: center;
    margin-top: 100px;
    justify-content: space-around;
    padding: 50px 0px;
    width: 100%;
    border-right: 0px;
    border-top: 2px solid #0f2d63;
  }
  .con-phone {
    margin-bottom: 0px;
  }
  .con-icons {
    font-size: 60px;
  }
  .contact-left h4 {
    font-size: 40px;
  }
  .contact-left p {
    font-size: 34px;
  }
  .btn-con {
    border-color: #2a4d8e;
    font-size: 14px;
  }
}
@media(max-width: 900px) {
