    line-height: 47px;
  }
  .contact-description {
    font-size: 17px;
  }
  input {
    font-size: 16px;
    padding: 15px 12px;
  }
  input:nth-child(4) {
    padding-bottom: 116px;
    margin-bottom: 30px;
  }
  .contact-left {
    display: flex;
    flex-direction: column;
  }
  .con-phone {
