.why{
  padding: 100px 0;
  background: #244685;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.why h2{
  text-transform: uppercase;
  font-size: 45px;
  text-align: center;
  margin-bottom: 80px;
  font-family: 'Varela Round', sans-serif;
  color: #67c9cb;
}
.why-item{
  width: 550px;
  margin-bottom: 70px;
  display: flex;
  margin-top: 40px;
  transition: 0.25s;
}
.why-img-wrapper{
  height: 115px;
  margin-top: -60px;
  margin-left: 60px;
}
.why-img-wrapper1{
  background: url(./../../../public/img/multimedia-code-2-outline.png);
  background-repeat: no-repeat;
  background-size: contain;
}
.why-img-wrapper2{
  background: url(./../../../public/img/technology-integrated-circuits-outline.png);
  background-repeat: no-repeat;
  background-size: contain;
}
.why-img-wrapper3{
  background: url(./../../../public/img/web-design-outline.png);
  background-repeat: no-repeat;
  background-size: contain;
}
.why-img-wrapper4{
  background: url(./../../../public/img/privacy-outline.png);
  background-repeat: no-repeat;
  background-size: contain;
}
.why-img-wrapper5{
  background: url(./../../../public/img/internet-outline.png);
  background-repeat: no-repeat;
  background-size: contain;
}
.why-img-wrapper6{
  background: url(./../../../public/img/run-outline.png);
  background-repeat: no-repeat;
  background-size: contain;
}
.why-text{
   display: flex;
   border:2px solid gray;
   width: 300px;
   height: 170px;
   border-radius: 15px;
}
.why-text span{
  transition: 1.1s;
  border-left:2px solid #244685;
  margin-left: -2px;
  height: 150px;
}
.why-text p{
  font-family: 'Varela Round', sans-serif;
  font-size: 22px;
  color: #f0f5f5;
  padding: 20px;
  width: 300px;
  transition: 0.45s;
}
.why-img{
  display: flex;
  flex-direction: column;
  margin-top: -100px;
}
.why-img p{
  transition: 0.45s;
  padding: 20px;
  color: #f0f5f5;
  text-transform: uppercase;
  font-size: 100px;
  font-family: 'Rammetto One', cursive;
  opacity: 0.6;
}
.why-img img{
  height: 115px;
  margin-top: -60px;
  align-self: flex-end;
}
.why-wrapper{
  flex-wrap: wrap;
  display: flex;
  max-width: 2000px;
  justify-content: space-between;
  padding: 50px;
}
.why-item:hover .why-text span{
  opacity: 0;
}
.why-item:hover .why-img p{
  opacity: 1;
}
.why-item:hover .why-text{
  border-color: #fff;
}
.why-item:hover .why-text p{
  opacity: 0.7;
}
.why-item:hover{
  transform: scale(0.95);
}
.why-item:hover .why-img-wrapper1{
  background: url(./../../../public/img/multimedia-code-2-outline.gif);
  background-repeat: no-repeat;
  background-size: contain;
}
.why-item:hover .why-img-wrapper2{
  background: url(./../../../public/img/technology-integrated-circuits-outline.gif);
  background-repeat: no-repeat;
  background-size: contain;
}
.why-item:hover .why-img-wrapper3{
  background: url(./../../../public/img/web-design-outline.gif);
  background-repeat: no-repeat;
  background-size: contain;
}
.why-item:hover .why-img-wrapper4{
  background: url(./../../../public/img/privacy-outline.gif);
  background-repeat: no-repeat;
  background-size: contain;
}
.why-item:hover .why-img-wrapper5{
  background: url(./../../../public/img/internet-outline.gif);
  background-repeat: no-repeat;
  background-size: contain;
}
.why-item:hover .why-img-wrapper6{
  background: url(./../../../public/img/run-outline.gif);
  background-repeat: no-repeat;
  background-size: contain;
}



