.about{
  padding-top: 100px;
  padding-bottom: 250px;
  background: #c4dcd8;
}
.about-mobile {
  display: none;
}
.about-main{
  background-image: linear-gradient(to left, #244685, #67c9cb);
  height: 430px;
  border-radius: 200px;
  padding-left: 200px;
  margin: 0 800px 0px -200px;
  display: flex;
  justify-content: space-around;
}
.about-img{
  align-self: center;
  position: relative;
  top: -180px;
right: 410px;
margin-left: 230px;
}
.about-text{
  margin-right: 100px;
}
.about-img img
{
  width: 350px;
  margin-right: 100px;
}
.fin1,.fin2{
  transition: ease-in-out .45s;
  top: 0;
  position: absolute;
}
.fin2{
 opacity: 0;
}
.about-main:hover{
  transition: 3s;
 background: linear-gradient(to left, rgba(103, 201, 203), rgba(36, 70, 133));
}
.about-main:hover .fin2{
  opacity: 1;
}
.about-main:hover .fin1{
  opacity: 0;
}
.about-main h2{
  text-align: left;
  padding-left: 30px;
  font-size: 45px;
  margin-bottom: 35px;
  transition: 0.35s;
  color: #fff;
  padding-top: 50px;
  text-transform: uppercase;
  font-family: 'Varela Round', sans-serif;
}
.about-main p{
  transition: ease-in-out 0.35s;
  font-family: 'Varela Round', sans-serif;
  font-size: 20px;
  line-height: 45px;
  text-align: left;
  letter-spacing: 1px;
  padding: 0 30px;
  color: #fff;
  width: 700px;
}




















